.category-button {
	padding: 0px 15px;
	display: flex;
	align-items: center;
	min-height: 45px;

	// .category-button__icon

	&__icon {
		display: inline-flex;
		stroke-width: 2px;

		&:not(:last-child) {
			margin-right: 15px;
		}
	}

	// .category-button__text

	&__text {
		font-size: 16px;
		font-weight: 500;
		line-height: 20 / 16 * 100%;
	}
}