.products-section {

	&:not(:last-child) {
		@include adaptiv-value("margin-bottom", 80, 50, 1);
	}

	// .products-section__body

	&__body {
		display: flex;
		flex-direction: column;
	}

	// .products-section__top

	&__top {
		&:not(:last-child) {
			margin-bottom: 30px;
		}
	}

	// .products-section__title

	&__title {
		@include adaptiv-value("font-size", 28, 22, 1);
		font-weight: 600;
		line-height: 34px;
		color: $colorDark;
	}

	// .products-section__slider

	&__slider {
		position: relative;
		overflow: hidden;

		.swiper-button-prev,
		.swiper-button-next {
			width: 40px;
			height: 40px;

			&:before {
				width: 9px;
				height: 16px;
			}
		}

		.swiper-button-disabled {
			opacity: 0;
		}
	}

	// .products-section__items

	&__items {
		gap: 20px;
		width: 100%;
		display: grid;
		grid-template-columns: repeat(5, 1fr);

		@media (max-width: 1199.98px) {
			grid-template-columns: repeat(4, 1fr);
		}

		@media (max-width: 991.98px) {
			grid-template-columns: repeat(3, 1fr);
		}

		@media (max-width: 768.98px) {
			grid-template-columns: repeat(2, 1fr);
		}

		@media (max-width: 479.98px) {
			grid-template-columns: repeat(1, 1fr);
		}
	}
}