.swiper-wrapper {
	display: inline-flex;
}

.swiper-button-prev,
.swiper-button-next {
	width: 35px;
	height: 35px;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	top: 50%;
	cursor: pointer;
	border-radius: 50%;
	transform: translateY(-50%);
	border: 1px solid $colorBorderGrey;
	background-color: $colorWhite;

	&:before {
		content: "";
		width: 8px;
		height: 14px;
		background-image: url("../../../public/img/icon/arrow.svg");
		background-size: contain;
		background-position: center;
		background-repeat: no-repeat;
	}
}

.swiper-button-prev {
	left: 10px;
}

.swiper-button-next {
	right: 10px;

	&:before {
		margin-right: -2px;
		transform: rotate(180deg);
	}
}