.menu {
	&:not(:last-child) {
		@include adaptiv-value("margin-right", 20, 15, 1);
	}

	// .menu__burger

	&__burger {}

	// .menu__body

	&__body {
		@include adaptiv-value("max-width", 425, 320, 1);
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: -100%;
		z-index: 200;
		transition: left .5s ease .0s;
		filter: drop-shadow(0px 6px 14px rgba(0, 0, 0, 0.2));
		border-right: 1px solid $colorBorderGrey;
		background-color: $colorWhite;

		&._active {
			left: 0;
		}

		@media (max-width: 374.98px) {
			max-width: none;
		}
	}

	// .menu__wrapper

	&__wrapper {
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: 0;
		opacity: 0;
		pointer-events: none;
		z-index: 190;
		transition: opacity .5s ease .0s;
		background-color: $colorWrapper;

		// .header__actions._active

		&._active {
			opacity: 0.5;
			pointer-events: auto;
		}
	}

	// .menu__top

	&__top {
		padding: 5px 15px;
		width: 100%;
		@include adaptiv-value("min-height", 80, 70, 1);
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-bottom: 1px solid $colorBorderGrey;
	}

	// .menu__content

	&__content {
		padding: 20px 15px;
	}
}

.menu-top {

	// .menu-top__logo

	&__logo {
		&:not(:last-child) {
			margin-right: 15px;
		}
	}

	// .menu-top__close

	&__close {}
}

.menu-content {}